<template>	
<section class="wrap has_header_wrap c_wrap">
	<!-- 头部标题 -->
	<com-header titles="收货地址">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->

	<!-- 地址列表 -->
	<div class="address_list">
		<div class="item" v-for="(al,index) in addList">
			<div class="item_top" @click="selectAddress(al,index)">
				<div class="d_flex">
					<span>{{ al.contactMan }}</span>
					<span>{{ al.mobile }}</span>
				</div>
				<p><!-- {{ al.districtID }}{{ al.city }}{{ al.county }} -->{{ al.address }}</p>
			</div>
			<div class="item_bottom d_flex">
				<div class="btns">
					<a class="inline_block"
						@click="jumpAdd(al,index)">编辑</a>
					<a 
						class="inline_block" 
						href="javascript:;"
						@click="delAdd(al,index)">删除</a>
				</div>
				<div class="default_add">
					<label isradio>
						<!-- <input checked type="radio" name="defaultAdd" value="1"> -->
						<input 
							:checked="al.isDefault"
							type="radio" 
							name="defaultAdd" 
							:value="index"
							@click="setDefaultAdd(al,index)">
						<i></i>
						<span>默认地址</span>
					</label>
				</div>
			</div>
		</div>
	</div>
	<!-- 地址列表 end -->
	
	<!-- 添加新地址 -->
	<button class="foot_btn" @click="jumpAdd"><i class="icons_add"></i><span>添加新地址</span></button>
	<!-- 添加新地址 end -->
</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle';
import comFoot from '@/components/Common/ComFoot';
import { Checklist, Button, MessageBox, Toast } from 'mint-ui';

export default {
	name: 'info',
	data (){
		return {
			addList: [],//地址列表
			urlParam: this.$route.query.form,//传递参数 purch来着下单页
		}
	},
	components: {
		comHeaderTitle, // 公共头部标题
		'mt-checklist': Checklist,
		'mt-button': Button,
	},
	filters: {
		
	},
	created (){
		const _self = this;
		
		this.$ajax({
			method: 'get',
			// url: _self.webRoot + '/delivery/getDeliveryAddress?memberId='+_self.$store.state.memberId,
			url: _self.webRoot + '/delivery/getDeliveryAddress?memberId='+_self.$store.state.memberId,
		})
		.then(function(res){
			// console.log(res)
			if( res.data.length === 0 ){
				// _self.$options.methods.showToast('没有地址');
				console.log('没有地址')
			}
			_self.addList = res.data;
			// _self.addList.unshift( res.data );
			// for( let i in res.data ){
			// 	_self.addList.unshift( res.data[i] );
			// }
		})
		.catch(function(err){
			console.log(err)
		});
	},
	mounted (){
		const _self = this;

		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//选择收货地址
		selectAddress (al,index){
			const _self = this;
			// console.log(al,index)
			if( this.urlParam ){ //来自下单页
				this.$store.commit('addBbuyProductAdd', al);
				sessionStorage.addBbuyProductAdd = JSON.stringify( al );
				// console.log(this.$store.state.buyProductAdd);
				this.$router.go(-1);
			}
		},
		// 跳转编辑/新增地址
		jumpAdd ( al,index ){
			if( index >= 0 ){
				// EventHandler.$emit( 'editAddEv', al );
				this.$store.commit('editAddressInfo', al);
			}
			
			this.$router.push({path:'/myinfo/add_address', query:{addressId: al.addressID}});
		},
		//设置默认地址
		setDefaultAdd (al,index){
			const _self = this;

			this.$ajax({
				url: _self.webRoot + '/delivery/isDefualt',
				// url: '/delivery/isDefualt',
				data: {
					memberID: _self.$store.state.memberId,
					addressID: al.addressID,
					isDefault: 1
				}
			})
			.then(function(res){
				// console.log(res)
				location.reload();
			})
			.catch(function(err){
				console.log(err)
			});
		},
		//删除
		delAdd(al,index) {
			const _self = this;

			MessageBox.confirm('确定执行此操作?').then(action => {
		  		_self.$ajax({
					// url: '/apiserver/delivery/delDeliveryAddress',
					url: _self.webRoot + '/delivery/delDeliveryAddress',
					data: {
						memberID: _self.$store.state.memberId,
						addressID: al.addressID,
					}
				})
				.then(function(res){
					// console.log(res)
					if( res.data.success ){
						_self.$options.methods.showToast('删除成功!');
						_self.addList.splice(index,1)
						setTimeout(function(){
							location.reload();
						},1000)
					}else{
						_self.$options.methods.showToast('删除失败!');
						setTimeout(function(){
							location.reload();
						},1000)
					}
					
				})
				.catch(function(err){
					console.log(err)
				});
			}).catch(function(err){
				console.log(err)
			});
		},

		//公共函数
		//显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/childre_wrap.css';
@import './Css/c_style.css';
@import './Css/address.css';
</style>


